/*UI EXPLANATION */

@keyframes show{
    0% {
        opacity:0;
      }
      100% {
        opacity:0.5;
      }
}
@keyframes show2{
    0% {
        opacity:0;
      }
      100% {
        opacity:1;
      }
}
/* FILL BAR ANIMATION FOR THE NAVBAR*/
@keyframes filling{
    0% {
        opacity: 0;
        width: 50%;
        left: -20%;
    }
    50%{
        opacity: 1;
    }
      100% {
        width: 50%;
        left: 50%;
        opacity: 0; 
    } 
}
@keyframes oppositefilling{
    0% {
        width: 50%;
        left: 90%;
        opacity: 0; 
    }
    50%{
        opacity: 1;
    }
      100% {
        opacity: 0;
        width: 50%;
        left: -20%;
    } 
}
/* FADE HOUSE ANIMATION */

@keyframes fadeHouse{
    0%{
        filter:  drop-shadow(0 0 3px whitesmoke);
        opacity: 1;
    }
    100%{
        filter:  drop-shadow(0 0 0px whitesmoke);
        opacity: 0.4;
    }
}
@keyframes illuminateHouse{
    0%{
        filter:  drop-shadow(0 0 0px whitesmoke);
        opacity: 0.4;
    }
    100%{
        filter:  drop-shadow(0 0 3px whitesmoke);
        opacity: 1;
    }
}





/**/







.App {
    text-align: center;
    display: grid;
    z-index: 1;
    position: relative;
    top: 0;
    overflow-x: hidden;
}

.navwrapper {
    display: grid;
    grid-template-columns: 25% 50% 25%;
    position: fixed;
    top: 0;
    min-width: 100%;
    color: white;
    height: 4rem;
    z-index: 2;
    background-color: rgb(13, 15, 32);
    justify-content: center;
    text-align: center;
    box-shadow: 0 0 19px 0 grey;
}
.navwrapper > h4 {
    display: grid;
    align-items: center;

}
.fi-xnsuxl-house-solid{
    font-size: 1.2rem;
}

.navwrapper > h4 > a {
    cursor: pointer;
    text-decoration: none;
    color: white;
    transition: all 0.2s ease-out;
    filter: drop-shadow(0 0 3px whitesmoke);

}
.navwrapper > h4 > a:hover {
    color: #7f8797;
    filter: drop-shadow(0 0 0px whitesmoke);
}


.buttonwrapper {
    display: grid;
    height: 80%;
    top: 10%;
    justify-items: center;
    position: relative;
}
.buttonwrapper > button {
    font-family: 'Open Sans', sans-serif;
    width: 75px;
    background-color: transparent;
    border: 0;
    padding: 0;
    color: white;
    filter: drop-shadow(0 0 3px whitesmoke);
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.5s ease-out;
}
.buttonwrapper > button:hover {
    color: rgb(196, 194, 194);
}


#canvas {
    display: grid;
    min-height: 100vh;
    min-width: 100vw;
    overflow: hidden;
    max-height: 2160px;
    max-width: 3840px;
}

/* CONTAINER ON TOP OF CANVAS*/

.contain {
    display: grid;
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
    max-height: 2160px;
    max-width: 3840px;
    left: 0;
    top: -100%;
    transition: all 0.5s ease-out;
}
.grid1_name_img > img {
    display: grid;
    position: fixed;
    left: 7.5%;
    top: 7.5%;
    max-height: 85%;
    min-height: 85%;
    max-width: 85%;
    min-width: 85%;
    box-shadow: 0 0 43px -4px rgba(0,0,0,0.75);
}
.grid1_name_img {
    pointer-events: none;
    /* ALLOWS THE ONMOUSEMOVE EVENT TO PASS THROUGH AND REGISTER THE CANVAS*/
    position: fixed;
    max-height: 60%;
    min-height: 60%;
    transform: rotate3d(1, -2, 0, 35deg);
    animation: popUp 2s normal forwards ease-in-out;
    max-width: 50%;
    min-width: 50%;
    display: grid;
    left: 0;
    top: 10%;
    z-index: 0;
    transition: all 0.1s ease-out;
}
.grid1_intro {
    display: grid;
    pointer-events: none;
    font-family: 'Open Sans', sans-serif;
    position: fixed;
    animation: popUp 2s normal forwards ease-in-out;
    color: white;
    top: 20%;
    min-width: 50%;
    max-width: 50%;
    left: 40%;
    max-height: 70%;
    min-height: 70%;
    transform: rotate3d(1, -2, 0, 35deg);
    z-index: 3;
    text-shadow: 2px 2px 2px black;
    transition: all 0.1s ease-out;
}
.grid1_intro > h4 {
    display: grid;
    margin: 0;
}
.grid1_intro > button {
    width: 50%;
    pointer-events: all;
    position: relative;
    left: 25%;
    font-family: 'Open Sans', sans-serif;
    color: white;
    border: 0;
    cursor: pointer;
    display: grid;
    font-size: 1.3rem;
    text-shadow: 2px 2px 2px black;
    border-radius: 8px;
    align-items: center;
    background-color: orangered;
    box-shadow: 0 0 43px -4px rgba(0,0,0,0.75);
    transition: background-color 0.5s ease-out;
}
.grid1_intro > button:hover {
    background-color: rgb(255, 146, 5);
}

.scrolldown {
    display: grid;
    position: fixed;
    color: white;
    justify-items: center;
    align-items: center;
    left: 90%;
    width: 7rem;
    height: 7rem;
    font-size: 0.8rem;
    opacity: 0.8;
    top: 10%;
    animation: popUpScroll 1s normal forwards ease-out;
    background-color: grey;
    border: 5px solid #fff;
    border-radius: 50%;
    box-shadow: 0 0 25px red , 0 0 25px whitesmoke;
    transition: all 0.3s ease-out;
}
.scrolldown > h3 {
    margin: 0;
    cursor: default;
    font-size: 0.8rem;
    text-shadow: -1px 0 5px rgb(255, 255, 255);
    width: 50%;
}

.transparentOverlay {
    display: grid;
    pointer-events: none;
    transform: rotate3d(1, -2, 0, 35deg);
    animation: popUp 2s normal forwards ease-in-out;
    position: fixed;
    min-width: 50%;
    max-width: 50%;
    max-height: 60%;
    min-height: 60%;
    left: 0;
    transition: all 0.1s ease-out;
    top: 10%;
    z-index: -1;
    background-color: grey;
    opacity: 0.5;
    box-shadow: 0 0 34px 18px grey;
}
.transparentOverlay2 {
    display: grid;
    transition: all 0.1s ease-out;
    position: fixed;
    pointer-events: none;
    transform: rotate3d(1, -2, 0, 35deg);
    animation: popUp 2s normal forwards ease-in-out;
    min-width: 50%;
    max-width: 50%;
    max-height: 70%;
    min-height: 70%;
    left: 40%;
    z-index: 1;
    top: 20%;
    background-color: grey;
    opacity: 0.5;
    box-shadow: 0 0 34px 18px grey;
}

.links {
    display: grid;
    max-height: 250px;
    min-height: 50px;
    grid-template-columns: 16.6666666667% 16.6666666667% 16.6666666667% 16.6666666667% 16.6666666667% 16.6666666667%;
}

.img_container {
    display: grid;
    max-height: 70%;
    max-width: 70%;
}

.img_container > img {
    display: grid;
    max-height: 100%;
    max-width: 100%;
}

/* ANIMATION FOR THE GRID 1 */
@keyframes popUp {
    from {
        opacity: 0;
        transform: scale(0, 0);
    }
    50% {
        opacity: 1;
        transform: scale(1.1, 1.1);
    }
    to {
        transform: scale(1, 1);
        transform: rotate3d(1, -5, 0, 35deg);
    }
}

/*POPUP FOR SCROLLDOWN*/
@keyframes popUpScroll {
    from {
        transform: scale(0, 0);
    }
    to {
        transform: scale(1, 1);
    }
}

/* REMOVING DIVS FROM THE SCENE */
@keyframes nextDiv {
    from {
        opacity: 0.6;
        transform: scale(1, 1);
        transform: rotate3d(1, -5, 0, 35deg);
    }
    25% {
        opacity: 0.6;
        transform: scale(1.1, 1.1);
    }
    50% {
        opacity: 1;
        left: 0;
        transform: scale(1, 1);
    }
    to {
        left: -150%;
    }
}

/* GRID 2 */

.grid2 {
    display: none;
    position: fixed;
    background-color: rgb(29, 27, 25);
    box-shadow: 0 0 25px 1px darkred;
    left: 15%;
    top: 4rem;
    opacity: 0.95;
    overflow-y: auto;
    min-width: 70%;
    max-width: 70%;
    height: 90vh;
    transition: all 0.2s ease-out;
}

/*GITHUB PORTFOLIO*/

.portfolioTitle > a > i {
    font-size: 200%;
    filter: drop-shadow(0 0 3px whitesmoke);
    transition: all 0.5s ease-out;
}
.portfolioTitle > a > i:hover {
    color: darkmagenta;
}





#grid2::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: #F5F5F5;
}

#grid2::-webkit-scrollbar {
    height: 10px;
    background-color: #F5F5F5;
}

#grid2::-webkit-scrollbar-thumb {
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #555;
}

.grid2_project {
    display: grid;
    grid-template-columns: 40% 60%;
    color: white;
    font-size: 85%;
    text-align: justify;
    border-bottom: 2px solid white;
    transition: all 0.2s ease-out;
    max-height: 40vh;
    min-height: 40vh;
}
.grid2_project:hover {
    background-color: rgb(43, 35, 26);
}

.textAndGit {
    display: grid;
    grid-template-rows: 60% 40%;
    margin: 2%;
}

.textAndGit > h4 {
    margin: 5%;
    cursor: default;
}
.project_img > img {
    display: grid;
    min-width: 100%;
    max-width: 100%;
    max-height: 50px;
    min-height: 100%;
    box-shadow: 4px 0 5px grey;
}

.project_wrapper_links > a {
    display: grid;
    justify-items: center;
    text-decoration: none;
    color: white;
    font-size: 200%;
    align-items: center;
    pointer-events: auto;
    cursor: pointer;
    transition: all 0.5s ease-out;
}
.project_wrapper_links > a:hover {
    color: darkmagenta;
}
.project_wrapper_links > a > i > svg {
    filter: drop-shadow(0 0 3px whitesmoke);
}

.project_wrapper_links > button {
    border: 0;
    border-radius: 10px;
    background-color: transparent;
    padding: 0;
    font-size: 120%;
    height: 50%;
    min-height: 50px;
    width: 50%;
}
.project_wrapper_links > button > a {
    display: grid;
    text-decoration: none;
    align-items: center;
    border: 0;
    border-radius: 10px;
    box-shadow: 0 0 5px whitesmoke;
    padding: 0;
    background-color: white;
    pointer-events: auto;
    color: black;
    font-family: 'Open Sans', sans-serif;
    height: 100%;
    width: 100%;
    transition: all 0.5s ease-out;
    cursor: pointer;
}
.project_wrapper_links > button > a:hover {
    background-color: darkmagenta;
    color: white;
}

.project_wrapper_links {
    display: grid;
    grid-template-columns: 50% 50%;
    min-height: 100%;
    justify-items: center;
    align-items: center;
}
@keyframes popGrid2 {
    from {
        transform: scale(0, 0);
        opacity: 0;
    }
    50% {
        transform: scale(1.1, 1.1);
        opacity: 0.5;
    }
    to {
        transform: scale(1, 1);
        opacity: 0.95;
    }
}
@keyframes popTic {
    from {
        transform: scale(0, 0);
        opacity: 0;
    }
    50% {
        transform: scale(1.1, 1.1);
        opacity: 0.5;
    }
    to {
        transform: scale(1, 1);
        opacity: 1;
    }
}
@keyframes popGrid2reverse {
    from {
        transform: scale(1, 1);
        opacity: 0.95;
    }
    50% {
        transform: scale(1.1, 1.1);
        opacity: 0.5;
    }
    to {
        transform: scale(0, 0);
        opacity: 0;
    }
}
@keyframes heartlikeAnim {
    from {
        transform: scale(1, 1);
    }
    25% {
        transform: scale(1.1, 1.1);
    }
    50% {
        transform: scale(0.8, 0.8);
    }
    75% {
        transform: scale(1.1, 1.1);
    }
    to {
        transform: scale(1, 1);
    }
}

/* TIC TAC TOE */

.TicTacToe {
    display: none;
    position: fixed;
    top: 10%;
    animation: popTic 1s normal forwards ease-out;
    pointer-events: all;
    left: 10%;
    min-height: 80%;
    max-height: 80%;
    min-width: 80%;
    max-width: 80%;
    z-index: 2;
    grid-template-rows: 2% 23% 75%;
    font-size: calc(10px + 2vmin);
    background-color: #282c34;
    box-shadow: 0 0 43px -4px rgba(0,0,0,0.75);
    color: white;
}
.TicTacToe > i {
    display: grid;
    width: 3%;
    max-height: 25px;
    min-height: 25px;
    left: 97%;
    position: relative;
    color: gray;
    transition: all 0.5s ease-out;
    cursor: pointer;
}
.TicTacToe > i > svg {
    display: grid;
    min-width: 100%;
    max-width: 100%;
    min-height: 100%;
    max-height: 100%;
}
.TicTacToe > i:hover {
    color: white;
}
.TicTacToe2 {
    display: grid;
    position: relative;
    align-items: center;
    left: 37.5%;
    max-width: 25%;
    min-width: 25%;
    max-height: 300px;
}
.TicTacToe2 > .logodiv {
    display: grid;
    max-height: 100%;
}
.scores {
    display: grid;
    grid-template-rows: 10% 20% 70%;
}
.title {
    display: grid;
}
.title > h3 {
    cursor: default;
    margin: 0;
}

.container {
    display: grid;
    width: 100%;
    grid-template-columns: 25% 50% 25%;
    justify-items: center;
    align-items: center;
}

.columncontainer {
    display: grid;
    position: relative;
    grid-template-rows: 33.3333333333% 33.3333333333% 33.3333333333%;
    box-shadow: 0 10px 37px 0 rgba(0,0,0,0.75);
    height: 300px;
    width: 300px;
}
.tictactoe {
    display: grid;
    margin: 0;
    grid-template-columns: 33.3333333333% 33.3333333333% 33.3333333333%;
}
.column {
    background-color: #9fa9bd;
    display: grid;
    margin: 3%;
    color: white;
    text-shadow: 2px 2px 2px rgba(150, 150, 150, 1);
    align-items: center;
    transition: background-color 0.5s ease-out;
    cursor: pointer;
}
.column:hover {
    background-color: #7f8797;
}
.container > button {
    display: grid;
    align-items: center;
    border: 0;
    border-radius: 12px;
    width: 80%;
    right: 10%;
    height: 60px;
    color: white;
    font-family: 'Open Sans', sans-serif;
    background-color: #6e7a90;
    transition: all 0.5s ease-out;
    text-shadow: 2px 2px 2px black;
    box-shadow: 0 0 43px -4px rgba(0,0,0,0.75);
    padding: 0;
    cursor: pointer;
}
.container > button:hover {
    background-color: darkmagenta;
}

